import React, {Component} from 'react';
import ReactTable from 'react-table';
import abi from '../ABIs/AurexiaSocialToken';
import Select from 'react-select';
import 'react-table/react-table.css';

var dateFormat = require('dateformat');

const Web3 = require('web3');
const infura = `https://ropsten.infura.io/v3/cc89e42528e441afb25d84e1499632ba`;
const web3 = new Web3(new Web3.providers.HttpProvider(infura));
const bip39 = require('bip39');
const ethers = require('ethers');
web3.eth.defaultAccount = "0xc4d446c6B924c431f89214319D5A3e6bb67e7627";

var compare_address = (address1, address2)=>{
    return address1.toUpperCase() === address2.toUpperCase();
}

class Users extends Component{

    constructor(props){
        super(props);
        this.contract = new web3.eth.Contract(abi, props.contractAddress, {
            from: web3.eth.defaultAccount ,
            gas: 10000000,
        });
        this.state = {
            select : [],
            users : [],
            transactions : [],
            add: false,
            delete: true,
            addname : "",
            addgrade : 1,
            delUser : ""
        }
        this.add_users = this.add_users.bind(this);
    }

    async UNSAFE_componentWillMount() {
        this.all_users = await this.get_all_users();
        this.get_users();
        this.setState({users: await this.get_all_users()});
        this.setState({transactions: await this.get_all_history()});
    }

    get_users = async() =>{
        var users = [];
        for(var i= 0; i < this.all_users.length; i++){
            if(this.all_users[i].name !== "Administrator"){
                users.push({value : this.all_users[i].address, label: this.all_users[i].name})
            }
        }
        this.setState({select : users});
    }

    get_user_name = async (address) =>{
        var name = new Promise(function(resolve, reject){
            try{
                this.contract.methods.getName(address).call((err, res) => {
                    let name = web3.utils.toAscii(res).split("\u0000")[0];
                    if (err) return reject(err);
                    resolve(name);
                })
            } catch(e){}
        })
        return await name;
    }

    get_name = async (address)=>{
        return (this.state.users.find(user => compare_address(user.address, address)))? this.state.users.find(user => compare_address(user.address, address)).name : await this.get_user_name(address);
    }

    get_all_history = async() =>{
        return new Promise((resolve, reject)=> {
            fetch('https://api-ropsten.etherscan.io/api?module=account&action=tokentx&contractaddress=0xaD57d1eD8A07Dcd5657eb7A1880C2Cc0C71257aE&startblock=0&endblock=999999999&sort=asc&apikey=NSAMUW521D6CQ63KHUPRQEERSW8FVRAF9B')
                .then(res =>{
                    res.json().then((data)=>{
                        this.get_All_wordings().then(async(messages)=>{
                            var res = data.result;
                            var history = [];
                            for(var i = res.length-1, j = 0; i>=0; i--){
                                if(messages[3][i] !== "Account Suppression"){
                                    var temp = res[i];
                                    history[j] = {};
                                    history[j].date = dateFormat(parseInt(temp.timeStamp)*1000, "mmmm dd yyyy");; //convert timestamp to date (*1000 below is to get it in ms)
                                    history[j].from = await this.get_name(temp.from);
                                    history[j].to = await this.get_name(temp.to);
                                    history[j].amount = Math.round(res[i].value * Math.pow(10,-18));
                                    history[j].message = messages[3][i];
                                    j++
                                }
                            }
                            resolve(history);
                        })
                    })
                })
        });
    }

    get_all_users = async() =>{
        return await new Promise((resolve, reject) => {
            this.contract.methods.getMembersAndNameAndBalance().call((err, res) =>{
                if(err) return reject(err);

                var users = [];
                for(var i = 0; i < res[0].length; i++){
                    users[i] = {};
                    users[i].address = res[0][i];
                    users[i].name = (web3.utils.toAscii(res[1][i])).split("\u0000")[0];
                    users[i].balance = Math.round((res[2][i])*Math.pow(10,-18));
                }
                resolve(users);
            })
        })
    }

    get_All_wordings = async() =>{
        return await new Promise((resolve, reject)=> {
            this.contract.methods.getAllWordings().call((err,res) =>{
                if(err) return reject(err);
                for(var i = 0; i< res[3].length; i++){
                        res[3][i] = web3.utils.toAscii(res[3][i]).split("\u0000")[0];
                }
                resolve(res);
            })
        })
    };

    wait(ms){
        var start = new Date().getTime();
        var end = start;
        while(end < start + ms) {
          end = new Date().getTime();
       }
    }

    sendMail(mnemonic, address) {
        window.emailjs.send(
                'outlook', 'aurexia_social_token_access',
                {nom: this.state.user.name, mnemonic: mnemonic, address: address, email_to: "soilhatmohamed@gmail.com"}
            ).then(res => {
              console.log('Email successfully sent!')
            })
            // Handle errors here however you like, or use a React error boundary
            .catch(err => console.error('Oh well, you failed. Here some thoughts on the error that occured:', err))
    }

    add_users = async ()=>{
        const mnemonic = bip39.generateMnemonic()
        let mnemonicWallet = ethers.Wallet.fromMnemonic(mnemonic);
        var address = mnemonicWallet.address;
        //this.sendMail(mnemonic, address);
        if (this.state.user.name !== "") 
            this.addM(address, this.state.user.name);
        this.state.user.name = "";
        this.state.add = !this.state.add;
    }

    addM = (address, name, grade=1) =>{
        this.contract.methods.addToAurexiaMembers(address, web3.utils.fromAscii(name), grade).estimateGas({from: web3.eth.defaultAccount})
        .then((gasAmount)=>{
            const tx = {
                from: web3.eth.defaultAccount,
                to: this.props.contractAddress,
                gas : gasAmount,
                data : this.contract.methods.addToAurexiaMembers(address, web3.utils.fromAscii(name), grade).encodeABI()
            }
            const signPromise = web3.eth.accounts.signTransaction(tx, "0xb22c556ba9ef9ef133550363fa40a8b38e995fdad1cb7a5b460a944ef61c669f");
            signPromise.then((signedtx)=>{
                const sentTx = web3.eth.sendSignedTransaction(signedtx.raw || signedtx.rawTransaction);
                sentTx.on("receipt", receipt => {
                    console.log(receipt)
                });
                sentTx.on("error", err => {
                    console.log(err)
                })
            }).catch((err) => {
                console.log(err)
            });
        })
        .catch((error)=>{
            console.log(error)
        })
    }

    delete_users = async(event)=>{
        var address = this.state.delUser.value;
        this.contract.methods.remAurexiaMembers(address).estimateGas({from: web3.eth.defaultAccount})
        .then((gasAmount)=>{
            const tx = {
                from: web3.eth.defaultAccount,
                to: this.props.contractAddress,
                gas : gasAmount,
                data : this.contract.methods.remAurexiaMembers(address).encodeABI()
            }
            const signPromise = web3.eth.accounts.signTransaction(tx, "0xb22c556ba9ef9ef133550363fa40a8b38e995fdad1cb7a5b460a944ef61c669f");
            signPromise.then((signedtx)=>{
                const sentTx = web3.eth.sendSignedTransaction(signedtx.raw || signedtx.rawTransaction);
                sentTx.on("receipt", receipt => {
                    console.log(receipt)
                });
                sentTx.on("error", err => {
                    console.log(err)
                })
            }).catch((err) => {
                console.log(err)
            });
        })
        .catch((error)=>{
            console.log(error)
        })
        event.preventDefault();
    }

    handleName=(e)=>{
        this.setState({addname : e.target.value})
    }

    handleGrade=(e)=>{
        this.setState({addgrade : e.target.value})
        e.preventDefault()
    }

    handleDelete = (event)=>{
        this.setState({delUser : event});
    }

    render(){
        return (
            <div>
                <div className="d-sm-flex align-items-center justify-content-between mb-4">
                    <h1 className="h3 mb-0 text-gray-800">{this.props.title}</h1>
                </div>
                <div className="row">
                    <div className="col-lg-12">
                        <div className="card shadow mb-4">
                            <div className="card-header py-3 d-flex flex-row align-items-center justify-content-between">
                                <h6 className="m-0 font-weight-bold text-primary">User Management</h6>
                                <div className="dropdown no-arrow">
                                    <div className="dropdown-toggle" role="button" id="dropdownMenuLink" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                        <i className="fas fa-plus fa-sm fa-fw text-gray-400"></i>
                                    </div>
                                    <div className="dropdown-menu dropdown-menu-right shadow animated--fade-in" aria-labelledby="dropdownMenuLink">
                                        <div className="dropdown-item" onClick = {() =>{this.setState({add : !this.state.add})}}>{(!this.state.add) ? "Add User(s)" : "Users List"}</div>
                                        <div className="dropdown-item" onClick = {() =>{this.setState({delete : !this.state.delete})}}>{(!this.state.delete) ? "Delete User(s)" : "Users List"}</div>
                                    </div>
                                </div>
                            </div>
                            <div className="card-body">
                                {!this.state.add && !this.state.delete &&
                                <div className="table-responsive table-editable">
                                    <ReactTable data={this.state.users} columns={[{
                                        Header: "Name",
                                        accessor: "name"
                                    },{
                                        Header: "Address",
                                        accessor: "address"
                                    },{
                                        Header: "Balance",
                                        accessor: "balance"
                                    }]}/>
                                </div>}
                                {this.state.add &&
                                <div>
                                    <div className="form-group">
                                        <input type="file"/>
                                    </div>
                                    Or
                                    <div className="form-group">
                                        <input type="text" className="form-control form-control-user" placeholder="Name" name="name" value={this.state.addname} onChange={this.handleName}/>
                                    </div>
                                    <div className="form-group">
                                        <select className="form-control form-control-user" name="grade" onChange={this.handleGrade} value={this.state.addgrade}>
                                            <option value="1">Consultant</option>
                                            <option value="2">Manager</option>
                                            <option value="3">Partner</option>
                                        </select>
                                    </div>
                                    <button className="btn btn-primary btn-user btn-block" onClick={this.add_users}>Add User</button>
                                </div>
                                }
                                {this.state.delete &&
                                <form className="form-group" onSubmit={this.delete_users}>
                                    <Select name="delAddress" options={this.state.select} value = {this.state.delUser} onChange={this.handleDelete}/>
                                    <br/>
                                    <button className="btn btn-primary btn-user btn-block">Delete User</button>
                                </form>
                                }
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-lg-12">
                        <div className="card shadow mb-4">
                            <div className="card-header py-3 d-flex flex-row align-items-center justify-content-between">
                                <h6 className="m-0 font-weight-bold text-primary">Users Transactions</h6>
                            </div>
                            <div className="card-body">
                                <div className="table-responsive table-editable">
                                    <ReactTable data={this.state.transactions} columns={[{
                                        Header : "Date",
                                        accessor: 'date'
                                    },{
                                        Header: "From",
                                        accessor: "from"
                                    },{
                                        Header: "To",
                                        accessor: "to"
                                    },{
                                        Header: "Amount",
                                        accessor: "amount"
                                    },{
                                        Header: "Description",
                                        accessor: 'message'
                                    }]}/>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Users;